* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

$primary:rgb(43, 42, 76);
$secondary: rgb(234, 144, 108);
$bg_color: rgb(238, 226 , 222);
nav {
  height: 11.6vh;
  
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background: $primary;
  .logo img {
    width: 100px;
    height: 100px;
    position: relative;
  }
  ul, li{
    display: flex;
    gap: 15rem;
    font-size: 2rem;
    text-decoration: none;
    
  }
}
.link {
  display: inline-block;
  position: relative;
  padding-bottom: 0.2rem;
  color: white;
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 0;
    height: 2px;
    background-color: $secondary ;
    transition: all 0.25s ease-out;
  }
  &:hover{
    color: $secondary;

  }
  &:hover::before {
    width: 100%;
    left: 0;
    right: auto;
  }
}

main{
  section{
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    height: calc(100vh - 200px);
    padding: 3rem;
    background-color: $bg_color;
    gap: 3rem;
    p{
      font-size: 1rem;
      padding-bottom: 1rem;
    }
    img{
      padding-left: 10rem;
    }
  }
}

footer{
  background-color: $primary ;
  padding: 1rem;
  p{
    color: white;
    font-size: 2rem;
    text-align: center;

  }
}